import _sample from 'lodash.sample';
import _shuffle from 'lodash.shuffle';
import './App.css';
import { useMemo, useState } from 'react';

const verbs: Array<string> = [
  'cocinar',
  'beber',
  'decir',
  'viajar',
  'correr',
  'trabajar',
  'dibujar',
  'hablar',
  'pasear',
  'amar',
  'leer',
  'vivir',
  'abrir',
  'escribir',
  'mejorar',
  'aprobar',
  'aprender',
  'descansar',
  'bailar',
  'tocar',
  'comprar',
  'nadar',
  'desayunar',
  'cenar',
  'besar',
];

enum SpanishPronouns {
  'Yo' = 'Yo',
  'T' = 'T',
  'Usted' = 'Usted',
  'l' = 'l',
  'Ella' = 'Ella',
  'Nosotros' = 'Nosotros',
  'Nosotras' = 'Nosotras',
  'Vosotros' = 'Vosotros',
  'Vosotras' = 'Vosotras',
  'Ellos' = 'Ellos',
  'Ustedes' = 'Ustedes',
}

enum VerbsEndings {
  'er' = 'er',
  'ar' = 'ar',
  'ir' = 'ir',
}

const table: Record<string, Record<string, string>> = {
  [VerbsEndings.er]: {
    [SpanishPronouns.Yo]: 'o',
    [SpanishPronouns.T]: 'es',
    [SpanishPronouns.Usted]: 'e',
    [SpanishPronouns.l]: 'e',
    [SpanishPronouns.Ella]: 'e',
    [SpanishPronouns.Nosotros]: 'emos',
    [SpanishPronouns.Nosotras]: 'emos',
    [SpanishPronouns.Vosotros]: 'is',
    [SpanishPronouns.Vosotras]: 'is',
    [SpanishPronouns.Ellos]: 'en',
    [SpanishPronouns.Ustedes]: 'en',
  },
  [VerbsEndings.ar]: {
    [SpanishPronouns.Yo]: 'o',
    [SpanishPronouns.T]: 'as',
    [SpanishPronouns.Usted]: 'a',
    [SpanishPronouns.l]: 'a',
    [SpanishPronouns.Ella]: 'a',
    [SpanishPronouns.Nosotros]: 'amos',
    [SpanishPronouns.Nosotras]: 'amos',
    [SpanishPronouns.Vosotros]: 'is',
    [SpanishPronouns.Vosotras]: 'is',
    [SpanishPronouns.Ellos]: 'an',
    [SpanishPronouns.Ustedes]: 'an',
  },
  [VerbsEndings.ir]: {
    [SpanishPronouns.Yo]: 'o',
    [SpanishPronouns.T]: 'es',
    [SpanishPronouns.Usted]: 'e',
    [SpanishPronouns.l]: 'e',
    [SpanishPronouns.Ella]: 'e',
    [SpanishPronouns.Nosotros]: 'imos',
    [SpanishPronouns.Nosotras]: 'imos',
    [SpanishPronouns.Vosotros]: 'is',
    [SpanishPronouns.Vosotras]: 'is',
    [SpanishPronouns.Ellos]: 'en',
    [SpanishPronouns.Ustedes]: 'en',
  },
}

function fillArrayWithRandomOptions<T>(arr: Array<T>, possibleOptions: Array<T>): Array<T> {
  const newArr = [...arr];
  while (newArr.length !== 4) {
    const randomOption = _sample<T>(possibleOptions) as T;
    if (newArr.includes(randomOption)) continue;
    newArr.push(randomOption);
  }
  return newArr;
}

const getAnswerOptions = (rightAnswer: string, pronoun: SpanishPronouns) => {
  let possibleOptions: Array<string>;
  switch (pronoun) {
    case SpanishPronouns.Yo:
    case SpanishPronouns.T:
    case SpanishPronouns.Usted:
    case SpanishPronouns.l:
    case SpanishPronouns.Ella:
    case SpanishPronouns.Ellos:
    case SpanishPronouns.Ustedes:
      possibleOptions = ['o', 'es', 'e', 'en', 'a', 'an', 'as', 'is']
      break;
    case SpanishPronouns.Nosotras:
    case SpanishPronouns.Nosotros:
    case SpanishPronouns.Vosotras:
    case SpanishPronouns.Vosotros:
    default:
      possibleOptions = ['imos', 'is', 'amos', 'is', 'emos', 'is', 'an', 'en']
      break;
  }
  
  const answers = fillArrayWithRandomOptions([rightAnswer], possibleOptions);

  return _shuffle(answers)
}

const getRandomWord = (words: Array<string>): string => {
  return _sample(words) || '';
}

const getSplittedWord = (verb: string): Array<string> => {
  return [verb.slice(0, verb.length - 2), verb.slice(-2)];
}

type TTestProps = {
  answer: string;
  withoutEnding: string;
  verbEnding: string;
  showAnswer: boolean;
  isAnswerValid: boolean;
  pronoun: string;
  nextWord: () => void;
}

type TAnswersProps = {
  rightAnswer: string;
  answers: Array<string>;
  selectAnswer: (answer: string) => void;
}

type TUseTest = {
  pronoun: SpanishPronouns;
  rightAnswer: string;
  answers: Array<string>;
  withoutEnding: string;
  verbEnding: string;
  handleSelectAnswer: (answer: string) => void;
  handleClickNextWord: () => void;
  showAnswer: boolean;
  isAnswerValid: boolean
}

const useTest = (): TUseTest => {
  const [verb, setVerb] = useState(getRandomWord(verbs));
  const [showAnswer, setShowAnswer] = useState(false);
  const [isAnswerValid, setIsAnswerValid] = useState(false);
  const [pronoun, setPronoun] = useState(_sample(Object.keys(SpanishPronouns)) as SpanishPronouns);
  const [withoutEnding, verbEnding] = getSplittedWord(verb);
  const rightAnswer = table[verbEnding][pronoun];
  const answers = useMemo(() => getAnswerOptions(rightAnswer, pronoun), [rightAnswer, pronoun]);

  const handleSelectAnswer = (selectedAnswer: string) => {
    setIsAnswerValid(selectedAnswer === rightAnswer);
    setShowAnswer(true);
    console.log('handleSelectAnswer')
  }

  const handleClickNextWord = () => {
    if (!showAnswer) return;

    console.log('handleClickNextWord')

    let newVerb = getRandomWord(verbs);
    while (newVerb === verb) {
      newVerb = getRandomWord(verbs);
    }

    setVerb(newVerb);
    setPronoun(_sample(Object.keys(SpanishPronouns)) as SpanishPronouns)
    setShowAnswer(false);
    setIsAnswerValid(false);
  }

  return {
    pronoun,
    rightAnswer,
    answers,
    withoutEnding,
    verbEnding,
    handleSelectAnswer,
    handleClickNextWord,
    showAnswer,
    isAnswerValid
  };
}

function Test(props: TTestProps): JSX.Element {
  
  const getBG = (): string => {
    if (props.showAnswer){
      return props.isAnswerValid ? 'isValid' : 'isInvalid';
    }
    return ''
  }

  return <div onClick={props.nextWord} className={`test ${getBG()}`}>
    <div className='text test_pronoun'>{props.pronoun}</div> 
    <div className='text test_verb'>{props.showAnswer ? `${props.withoutEnding}${props.answer}` : `${props.withoutEnding}(${props.verbEnding})`}</div>
  </div>
}

function Answers(props: TAnswersProps): JSX.Element {
  return <div className='answers'>
    {props.answers.map((answerOption: string) => {
      return <button key={answerOption} onClick={() => props.selectAnswer(answerOption)} className='answer-btn'>{answerOption}</button>
    })}
  </div>
}

function App() {

  const {
    pronoun,
    rightAnswer,
    answers,
    withoutEnding,
    verbEnding,
    handleSelectAnswer,
    handleClickNextWord,
    showAnswer,
    isAnswerValid
  } = useTest();

  return (
    <div className="App">
        <Test
          nextWord={handleClickNextWord}
          isAnswerValid={isAnswerValid}
          showAnswer={showAnswer}
          pronoun={pronoun}
          answer={rightAnswer}
          withoutEnding={withoutEnding}
          verbEnding={verbEnding}
        />
        <Answers selectAnswer={handleSelectAnswer} answers={answers} rightAnswer={rightAnswer} />
    </div>
  );
}

export default App;
